import React, {useEffect, useState} from 'react';
import {useLocation} from 'react-router-dom';
import Modal from 'react-modal';
import InfiniteScroll from 'react-infinite-scroll-component';

import {custom} from '../custom';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {items as ShopItem} from '../items';
import {hooks, RootState} from '../hooks';

const sortingBy = [
  {id: 1, title: 'Sale'},
  {id: 2, title: 'Top'},
  {id: 3, title: 'Newest'},
  {id: 4, title: 'Price: low to high'},
  {id: 5, title: 'Price: high to low'},
];

export const Shop: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const location = useLocation();
  const title = location.state.title;
  const products = location.state.products;

  const [sort, setSort] = useState(sortingBy[0]);
  const [modalIsOpen, setIsOpen] = React.useState(false);

  const [items, setItems] = useState(products.slice(0, 20));
  const [hasMore, setHasMore] = useState(products.length > 20);

  const {selectedColors, selectedSizes, selectedCategories, selectedTags} =
    hooks.useAppSelector((state: RootState) => state.filterSlice);

  const filteredProducts = products.filter((product: any) => {
    const sizeMatch =
      selectedSizes.length === 0
        ? true
        : selectedSizes.some(size => product.sizes.includes(size));

    const colorMatch =
      selectedColors.length === 0
        ? true
        : selectedColors.some(selectedColor =>
            product.colors.some(
              (productColor: any) => productColor.name === selectedColor,
            ),
          );

    const tagMatch =
      selectedTags.length === 0
        ? true
        : selectedTags.some(tag => product.tags.includes(tag));

    const statusMatch =
      selectedCategories.length === 0
        ? true
        : (product.isNew && selectedCategories.includes('new')) ||
          (product.isTop && selectedCategories.includes('top')) ||
          (product.oldPrice && selectedCategories.includes('sale'));

    return colorMatch && statusMatch && tagMatch && sizeMatch;
  });

  const sortedProducts = [...filteredProducts].sort((a, b) => {
    switch (sort.title) {
      case 'Price: low to high':
        return a.price - b.price;
      case 'Price: high to low':
        return b.price - a.price;
      case 'Newest':
        return a.isNew === b.isNew ? 0 : a.isNew ? -1 : 1;
      case 'Top':
        return a.isTop === b.isTop ? 0 : a.isTop ? -1 : 1;
      case 'Sale':
        return a.oldPrice === b.oldPrice ? 0 : a.oldPrice ? -1 : 1;
      case 'Featured':
        return a.isFeatured === b.isFeatured ? 0 : a.isFeatured ? -1 : 1;
      default:
        return 0;
    }
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const fetchMoreData = () => {
    if (items.length >= products.length) {
      setHasMore(false);
      return;
    }

    setTimeout(() => {
      setItems(items.concat(products.slice(items.length, items.length + 20)));
    }, 500);
  };

  const renderHeader = () => {
    return (
      <header
        style={{
          top: 0,
          position: 'sticky',
          display: 'flex',
          flexDirection: 'column',
          backgroundColor: theme.colors.white,
          zIndex: 4,
        }}
      >
        {/* Top Info */}
        <div
          style={{
            height: 52,
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
          }}
        >
          <button
            style={{
              position: 'absolute',
              left: 0,
              padding: 20,
              cursor: 'pointer',
              backgroundColor: 'transparent',
              border: 'none',
            }}
            onClick={() => navigate(-1)}
          >
            <svg.GoBackSvg />
          </button>
          <h4
            style={{
              width: 'calc(100% - 100px)',
              textAlign: 'center',
              position: 'absolute',
              left: '50%',
              transform: 'translateX(-50%)',
              ...theme.fonts.Mulish_600SemiBold,
              fontSize: 18,
              color: theme.colors.mainColor,
            }}
          >
            {title}
          </h4>
          <button
            // onClick={handleOnClick}
            style={{
              position: 'absolute',
              right: 0,
              // padding: 20,
              paddingTop: 12,
              paddingBottom: 12,
              paddingLeft: 20,
              paddingRight: 20,
              borderRadius: 12,
              // paddingTop: 18,
              // paddingBottom: 18,
              // paddingLeft: 20,
              // paddingRight: 20,
              // backgroundColor: 'red',
            }}
          >
            <svg.BasketSvg />
            <div
              style={{
                paddingLeft: 6,
                paddingRight: 6,
                borderRadius: 10,
                right: 35,
                bottom: 10,
                position: 'absolute',
                display: 'flex',
                paddingTop: 2,
                paddingBottom: 2,
                fontSize: 10,
                color: theme.colors.white,
                ...theme.fonts.Mulish_700Bold,
                backgroundColor: theme.colors.coralRed,
              }}
            >
              {/* {cart.length > 0 ? `$${total.toFixed(2)}` : '$0'} */}0
            </div>
          </button>
        </div>
        {/* Filters and sorting */}
        <div
          style={{
            display: 'flex',
            alignItems: 'baseline',
            justifyContent: 'space-between',
            padding: '0 20px',
            paddingBottom: 19,
          }}
        >
          <button
            onClick={() => {
              navigate('/Filter');
            }}
          >
            <svg.FiltersSvg />
          </button>
          <button
            onClick={() => {
              setIsOpen(true);
            }}
          >
            <svg.SortingBySvg />
          </button>
        </div>
      </header>
    );
  };

  const renderContent = () => {
    return (
      <div>
        <custom.InfiniteScrollR
          data={products}
          endMessage={true}
          style={{paddingBottom: 20}}
        >
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              flexWrap: 'wrap',
              justifyContent: 'space-between',
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            {filteredProducts.map((reviewItem: any, index: any, array: any) => {
              const isLast = index === array.length - 1;
              return (
                <ShopItem.ProductCard
                  key={reviewItem.id}
                  isLast={isLast}
                  item={reviewItem}
                  version={2}
                />
              );
            })}
          </div>
        </custom.InfiniteScrollR>
      </div>
    );
  };

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <>
      {renderHeader()}
      {renderContent()}
      <Modal
        isOpen={modalIsOpen}
        contentLabel='Example Modal'
        onRequestClose={closeModal}
        onAfterOpen={() => {
          // subtitle.style.color = '#f00';
        }}
        shouldCloseOnOverlayClick={true}
        style={{
          content: {
            width: 'calc(100% - 80px)',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            position: 'absolute',
            top: 'calc(50% - 52px + 76px)',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            // border: 'none',
            margin: 0,
            padding: 0,
            borderRadius: 5,
            zIndex: 5,
            // height: 0,
            borderColor: theme.colors.lavenderMist,
            // maxHeight: '80vh', //   
            // overflowY: 'auto', //    ,     
          },
        }}
      >
        <div
          style={{
            display: 'flex',
            flex: 1,
            flexDirection: 'column',
            justifyContent: 'flex-end',
            alignItems: 'flex-start',
            // backgroundColor: 'orange',
            // paddingTop: 10,
          }}
        >
          {sortingBy.map(item => {
            return (
              <div
                style={{
                  // display: 'flex',
                  // alignItems: 'center',
                  // justifyContent: 'center',
                  // width: '100%',
                  padding: '20px 20px 15px 20px',
                  paddingBottom: 18,
                  borderBottom: 'solid #EDF1FA 1px',
                  width: '100%',
                  ...theme.fonts.Mulish_400Regular,
                  color: theme.colors.textColor,
                }}
              >
                {item.title}
              </div>
            );
          })}
        </div>

        {/* <button
          onClick={() => {
            setIsOpen(false);
          }}
        >
          close
        </button> */}
      </Modal>
    </>
  );
};
